.projectContainer{
    /* background-color: #d1e9ff !important; */
    width: 100% !important;
    /* padding-top: 1%; */
    /* padding-left: 1%; */
    /* padding-right: 1%; */
    /* padding-bottom: 1%; */
    border: 1px solid #1f1f1f;
    border-radius: 10px;
    margin: 0.2%;
}

.projectTitle{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.projectTitleText{
    font-size: 1.5rem !important;
    font-weight: 700 !important;
    text-align: justify;
    padding-bottom: 1%;
    color: #1f1f1f;
    padding-left: 2%;
    text-transform: capitalize !important;
}

.projectDetailsRow {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.projectCard {
    display: flex;
    flex-direction: column;
    background-color: #e5e5e5;
    border-radius: 10px;
    justify-content: center;
    width: 50%;
    margin-left: 1%;
    margin-right: 1%;
    margin-bottom: 1%;
    padding: 0.5%;
}

.projectCardRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 1%;
    align-items: center;
}

.projectCardText{
    font-size: 0.75rem !important;
    font-weight: 500 !important;
    width: 87%;
    padding-top: 1%;
    margin-left: 1%;
    text-align: justify;
}