.topContainer {
    display: flex;
    flex-direction: column;
    /* background-color: #d9d9d9; */
    color: #0b4b7e;
    text-align: center;
}

.profilePhoto{
    display: block;
    /* flex-direction: row;
    justify-content: space-around; */
    margin-left: 27% !important;
    margin-top: 1.5%;
    height: 14rem;
    width: 14rem;
    border-radius: 50%;
    border: 5px solid #66A5CC;
    /* margin: 1%; */
}

.pageTitle {
    font-size: 2.5rem !important;
    font-weight: 700 !important;
}

.designationPanel{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
}

.designationRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 1%;
}

.designationText{
    display: block;
    font-size: 0.75rem !important;
    font-weight: 500 !important;
    width: 87%;
    padding-top: 2%;
    margin-left: 5%;
    text-align: justify;
    align-items: center;
}

.profTrainingImage{
    height: 1.13rem;
    width: 2.85rem;
    margin-top: 1%;
    margin-left: 5%;
    margin-left: 5%;
}

.workExpImage{
    height: 2.15rem;
    width: 2.85rem;
    margin-top: 1%;
    margin-left: 5%;
    margin-left: 5%;
}

.eduImage{
    height: 2.85rem;
    width: 2.85rem;
    margin-top: 1%;
    margin-left: 5%;
    margin-left: 5%;
}